import React from 'react';
import { Space, Popconfirm } from 'antd';
import {
 EditOutlined,
 DeleteOutlined,
 KeyOutlined,
 EyeOutlined,
 RotateRightOutlined,
} from '@ant-design/icons';
import { getRoles } from '../../../function/fn';
import { Link } from 'react-router-dom';

export const landCol = ({
 handleDelete,
 handleUserEdit,
 handleAccountEdit,
 handleUserRole,
 setRoleUserID,
 limit,
 page,
 landOwner,
}) => {
 // let l = limit >= 20 ? limit/page : limit
 // let no = ((page-1) * l)
 let no = 100;
 var array = [
  {
   title: 'ID',
   dataIndex: 'id',
   key: 'id',
   width: 50,
   // render: (text, record) => (
   //     <Space size="middle">
   //         {no+=1}
   //     </Space>
   // ),
  },

  {
   title: '',
   dataIndex: 'idLand',
   key: 'idLand',
   width: 50,
  },
  {
   title: '',
   dataIndex: 'address',
   key: 'address',
   width: 120,
   render: (text, record) => (
    <Space size="middle">
     {'' +
      record?.add?.vil +
      ',' +
      record?.add?.com +
      ',' +
      record?.add?.dis +
      ',' +
      record?.add?.pro}
    </Space>
   ),
  },

  {
   title: '',
   dataIndex: 'owner',
   key: 'owner',
   width: 50,
   render: (text, record) => (
    <Space size="middle">{record?.owner?.ownerId}</Space>
   ),
  },
  {
   title: '',
   dataIndex: 'landType',
   key: 'landType',
   width: 80,
  },

  {
   key: 'action',
   dataIndex: 'action',
   fixed: 'right',
   width: 50,
   align: 'center',
   render: (text, record) => (
    <Space size="middle">
     {/* <span className="link" onClick={() => handleAccountEdit(record)}><KeyOutlined /></span> */}
     <Link
      to={'/ownerdetail/' + record?.owner?.ownerId + '?landId=' + record.id}
      className="link"
     >
      <EyeOutlined />
     </Link>

     <Popconfirm
      title="?"
      onConfirm={() => {
       handleDelete(record.id);
      }}
      okText=""
      cancelText=""
     >
      <span className="link" style={{ color: 'red' }}>
       <DeleteOutlined />
      </span>
     </Popconfirm>
     <Link className="link">
      <RotateRightOutlined />
     </Link>
    </Space>
   ),
  },
 ];

 let newArr = array.filter(
  (key) => key.key !== 'address' && key.key !== 'owner'
 );

 if (landOwner === true) {
  return newArr;
 }

 return array;
};
